import Canvas from '../../components/Canvas';

export default class Home {

    constructor(root, config) {
        this._root = root;
        this._main = document.getElementById('main');

        this._canScroll = false;
        this._currSection = 1;
        this._countSection = 3;
        this._isSkip = false;

        if(this._main){
            this._mainWrapper = document.getElementById('main-wrapper');
            this._mainText = document.querySelector('#main .main__main-text');
            this._mainLine = document.querySelector('#main .main__line');
            this._mainDescription = document.querySelector('#main .main__description');
            this._mainVideoBg = document.querySelector('#main .main__video');
            this._mainSection = document.querySelectorAll('#main .main__section');
            this._mainSkip = document.querySelector('.main__skip');

            this._startVideo();
            this._initScrollableSection();
            this._initScene();
            this._initSkip();

        }
    }

    _initSkip(){
        this._mainSkip.addEventListener('click', () => {
            this._skip();
        })
    }

    _skip(){
        this._isSkip = true;
        this._canScroll = true;
        this._currSection = 3;
        this._mainDescription.classList.add('main__description--fast-anim');
        this._mainWrapper.classList.add('main__wrapper--fast-scroll');
        this._mainVideoBg.currentTime = 100;
        this._moveSection();
    }

    /**
     * Инициализация 3d сцены
     *
     * @private
     */
    _initScene(){
        if(Detector.webgl){
            this._createScene();
        }else{
            this._root.querySelector('.main__space-man').style.display = 'block';
            console.warn('Ваш браузер или устройство не поддерживает WebGL');
        }
    }

    /**
     * Создает сцену и загружает 3d модель
     *
     * @private
     */
    _createScene(){
        this._sceneRoot = this._root.querySelector('.main__3d-scene');

        if(this._sceneRoot){
            this._scene = new Canvas(this._sceneRoot, {
                camera: {
                    position: new THREE.Vector3(0,1.5,5),
                    lookAt: new THREE.Vector3(0,0.25,0),
                    size: 1.3
                },
                light: {
                    position: new THREE.Vector3(0,20,50)
                }
            });
            this._scene.loadModel( '/model/luk_im_your_astronaut_black2.JD' );

            let timeAnimation = 4.5;

            this._root.addEventListener('mousemove', (event) => {
                let percent = event.screenX / window.innerWidth;
                let time = timeAnimation * percent;
                this._scene.animateToTime(time);
            })
        }
    }

    /**
     * Инициализирует и запускает видео
     *
     * @private
     */
    _startVideo(){

        let startLine = () => {
            this._mainLine.classList.add('main__line--active');
            setTimeout( () => {
                this._canScroll = true;
                if(!this._isSkip) {
                    this._currSection++;
                    this._moveSection();
                }
            }, 500 );
        };

        this._mainVideoBg.addEventListener('play', () => {
            console.log('play bg');
        });

        this._mainVideoBg.addEventListener('ended', () => {
            console.log('stop bg');
            this._mainSkip.classList.remove('main__skip--active');
            startLine()
        });

        if(window.innerWidth > 992) {
            if(!localStorage['was_video_play']){
                localStorage['was_video_play'] = true;
                this._mainVideoBg.play();
                setTimeout(() => {
                    this._mainSkip.classList.add('main__skip--active')
                }, 800);

            }else{
                this._skip();
            }

        }else{
            this._mainVideoBg.style.display = 'none';
            this._mainText.classList.add('main__main-text--active');
            setTimeout( () => {
                startLine();
            }, 2000 );
        }
    }

    /**
     * Переходит к слайду
     *
     * @param indSlide {Number}
     * @param callback {function}
     * @private
     */
    _slideTo(indSlide, callback){
        if(!this._currSectionClass)
            this._currSectionClass = 'main__wrapper--section-1';
        this._mainWrapper.classList.remove(this._currSectionClass);
        this._currSectionClass = 'main__wrapper--section-' + indSlide;
        this._mainWrapper.classList.add(this._currSectionClass);

        if(indSlide === 1){
            this._mainDescription.classList.remove('main__description--hide');
            this._mainDescription.classList.remove('main__description--active');
        }

        if(indSlide === 2){
            setTimeout(() => {
                this._mainDescription.classList.remove('main__description--hide');
                this._mainDescription.classList.add('main__description--active');
            }, 1000);
        }

        if(indSlide === 3){
            this._mainDescription.classList.add('main__description--hide');
        }

        setTimeout(() => {
            if(typeof callback === "function") callback();
        }, 2300)
    }

    /**
     * Инициализирует обратоку скрола мыши
     *
     * @private
     */
    _initScrollableSection(){

        this._mainWrapper.addEventListener('wheel', (e) => {
            this._handlerScrollWheel(e)
        })
    }

    _handlerScrollWheel(e){
        // console.log(e);
        if(this._canScroll){

            this._canScroll = false;

            if(e.deltaY > 0) {
                if (++this._currSection > this._countSection) {
                    this._currSection--;
                    this._canScroll = true;
                    return;
                }
            } else {
                if (--this._currSection < 1) {
                    this._currSection++;
                    this._canScroll = true;
                    return;
                }
            }
            this._mainDescription.classList.remove('main__description--fast-anim');
            this._mainWrapper.classList.remove('main__wrapper--fast-scroll');
            this._moveSection();

        }
    }

    _moveSection(){
        this._slideTo(this._currSection, () => {
            this._canScroll = true;
        });
    }
}