import OwlSlider from '../../components/OwlSlider'

export default class Mixes{

    constructor(root, config, globalConfig){
        this._root = root;

        this._laptop = this._root.querySelector('.laptop');
        this._laptopBg = this._laptop.querySelector('.laptop__bg');

        // this._initChangeSection();
        this._initTabs();
        this._initSliders();
        this._initPopups();
    }

    _initChangeSection(){
        let sectionWrap = this._root.querySelector('.map-mixes__wrapper');
        let spaceManSeq = this._root.querySelectorAll('.map-mixes__space-man');

        if(spaceManSeq){
            let startAnimate = 100;
            let offsetSeqAnimate = 700;
            spaceManSeq.forEach( item => {
                setTimeout( () => {
                    spaceManSeq.forEach( item => item.classList.remove('map-mixes__space-man--active') );
                    item.classList.add('map-mixes__space-man--active');
                }, startAnimate );
                startAnimate += offsetSeqAnimate;
            } );

            if(sectionWrap){
                setTimeout(() => {
                    sectionWrap.classList.add('map-mixes__wrapper--section-2');
                }, startAnimate + 1000)
            }
        }
    }

    _initPopups(){
        $('.js-mix-popup').magnificPopup({
            type: 'ajax',
            removalDelay: 300,
            mainClass: 'mfp-fade mix-popup',
            gallery: {
                enabled: true
            },
            preloader: false,
            fixedContentPos: true,
            callbacks: {
                change: function () {
                    $(this.content).fadeOut();
                    //console.log(this);
                    //console.log('change');
                },
                open: function() {
                    console.log('open');
                    //overwrite default prev + next function. Add timeout for css3 crossfade animation
                    /*$.magnificPopup.instance.next = function() {
                        let self = this;
                        self.wrap.removeClass('mfp-image-loaded');
                        setTimeout(function() { $.magnificPopup.proto.next.call(self); }, 1000);
                    };
                    $.magnificPopup.instance.prev = function() {
                        let self = this;
                        self.wrap.removeClass('mfp-image-loaded');
                        setTimeout(function() { $.magnificPopup.proto.prev.call(self); }, 1000);
                    };*/
                },
                parseAjax: function (response) {
                    console.log('ajax parse');
                    console.log(arguments);
                    return false;
                },
                ajaxContentAdded: function () {
                    console.log('ajax added');
                    console.log(arguments);
                    return false;
                }
            },
        });

        document.addEventListener('click', e => {
            if(e.target.parentNode.classList.contains('popup__arrow')) {
                let magnificPopup = $.magnificPopup.instance;
                if (e.target.parentNode.classList.contains('popup__arrow--prev')) {
                    magnificPopup.prev();
                    return;
                }
                if (e.target.parentNode.classList.contains('popup__arrow--next')) {
                    magnificPopup.next()
                }
            }
        })
    }

    _initTabs(){
        this._tabsHolder = this._laptop.querySelector('.laptop__tabs');
        this._tabs = this._tabsHolder.querySelectorAll('.laptop__tab');
        this._tabContents = new Map();

        for(let tab of this._tabs){
            let tabId = tab.getAttribute('data-target');
            let tabContent = document.querySelector(tabId);
            this._tabContents.set( tabId, tabContent );
            if(tab.classList.contains('laptop__tab--active')){
                tabContent.style.display = 'block';
                tabContent.querySelector('.owl-carousel').classList.remove('owl-hidden');
            }
            ['click', 'touchstart'].forEach(
                event => tab.addEventListener( event, () => {
                    for(let tab of this._tabs){
                        tab.classList.remove('laptop__tab--active');
                    }
                    for( let [id, tabContent] of this._tabContents ){
                        tabContent.style.display = 'none';
                    }
                    tab.classList.add('laptop__tab--active');
                    tabContent.style.display = "block";
                } )
            );
        }

        window.addEventListener('resize', () => {
            this._limitLaptop();
        });
        this._limitLaptop();
    }

    _initSliders(){
        this._slider = new OwlSlider('.laptop__slider .owl-carousel', {
            items: 1,
            nav: true,
            dots: false,
            navText: [
                '<div class="mix-slider__arrow mix-slider__arrow--prev"><img src="/images/arrow-left.svg" alt=""></div>',
                '<div class="mix-slider__arrow mix-slider__arrow--next"><img src="/images/arrow-right.svg" alt=""></div>'
            ]
        });
    }

    _limitLaptop() {
        setTimeout(
            () => {
                let laptopHeight = window.innerHeight - this._tabsHolder.clientHeight - 56;
                this._laptopBg.style.maxHeight = `${laptopHeight}px`;
            }
        , 100);
    }
}
