import OwlSlider from '../../components/OwlSlider'
import Dots from '../../components/Dots'

export default class MapMixesMobile {
    constructor(root, config, configGlobal) {
        this._root = root;
        this._config = config;
        this._configGlobal = configGlobal;

        this._owlSlider = new OwlSlider(this._config.owlSliderSelector, this._configGlobal.owlSliderConfig);
        this._dots = new Dots(this._config.dotsSelector, this._config.dots);

        //
        this._initModal()
    }

    _initModal() {
        let _this = this;
        $('.map-mixes-mobile__item-mix').magnificPopup({
            type: 'ajax',
            mainClass: 'modal-mobile mfp-fade',
            callbacks: {
                open: function() {
                    $('html').css('margin-right', '');
                },
                parseAjax: function(mfpResponse) {
                    //console.log(mfpResponse.data);
                    mfpResponse.data = $(mfpResponse.data).filter('.modal-mobile__wrapper');
                    //console.log(mfpResponse.data);
                },
                ajaxContentAdded: function() {
                    new OwlSlider('.modal-mobile .owl-carousel', _this._config.owlSliderConfig);
                }
            }
        });
    }
}