import Canvas from '../../components/Canvas'

export default class Enter {
    constructor(root, config) {
        this._root = root;
        this._init();
    }

    _init(){
        if(this._root){
            this._enter = document.querySelector('.enter');
            this._dots = document.getElementById('dots');
            if(this._dots){
                this._dotsHolder = document.querySelector('#dots .enter__dots-wrapper');
                if(this._dotsHolder) {
                    this._generateDots();
                }
            }

            if(Detector.webgl){
                this._initModel();
            }else{
                document.querySelector('.enter__head').style.display = 'block';
                console.warn('Ваш браузер или устройство не поддерживает WebGL')
            }
        }
    }

    _initModel(){
        console.log('init model');

        if(window.innerWidth < 768) return;

        let sceneRoot = document.querySelector('.enter__scene');

        if(sceneRoot){

            let posY = 241;

            this._scene = new Canvas(sceneRoot, {
                camera: {
                    position: new THREE.Vector3(0,posY,50),
                    lookAt: new THREE.Vector3(0,posY,0),
                    size: 45
                },
                light: {
                    position: new THREE.Vector3(0,20,50)
                }
            });

            this._scene.loadModel('/model/head_27_07_.JD', () => {

                //this._scene.removeMesh(0);

                //this._scene.addShaderToMesh(0, Canvas.SHADERS.OUTLINE);

                let startRotateY = this._scene.getMeshes()[0].rotation.y;
                let startRotateX = this._scene.getMeshes()[0].rotation.x;

                this._enter.addEventListener('mousemove', (event) => {
                    let lengthFromMiddleW = window.innerWidth * 0.5 -  event.clientX;
                    let lengthFromMiddleH = window.innerHeight * 0.5 -  event.clientY;
                    let degY = startRotateY - ( 0.0002 * lengthFromMiddleW);
                    let degX = startRotateX - ( 0.0002 * lengthFromMiddleH);
                    this._scene.rotateScene(new THREE.Vector3(degX, degY, 0));
                });
            });
        }
    }

    _generateDots(){
        let width = this._dotsHolder.clientWidth;
        let height = this._dotsHolder.clientHeight;

        this._countDotsW = 40;
        this._countDotsH = 23;

        let offsetW = width / this._countDotsW;
        let offsetH = height / this._countDotsH;

        this._dotsArray = [];

        for(let i = 0; i < this._countDotsH + 1; i++){
            for(let j = 0; j < this._countDotsW + 1; j++) {

                let dot = document.createElement('div');
                dot.className = 'enter__dot';
                dot.style.top = (i * offsetH) + 'px';
                dot.style.left = (j * offsetW) + 'px';
                this._dotsArray.push(dot);
                this._dotsHolder.appendChild(dot);
            }
        }

        window.addEventListener('resize', (event) => {
            // do stuff here
            // console.log('resize');
            setTimeout(() => {
                this._resizeDots();
            });
        });
    }

    _resizeDots(){
        let width = this._dotsHolder.clientWidth;
        let height = this._dotsHolder.clientHeight;

        let offsetW = width / this._countDotsW;
        let offsetH = height / this._countDotsH;

        for(let i = 0; i < this._countDotsH + 1; i++){
            for(let j = 0; j < this._countDotsW + 1; j++) {
                let dot = this._dotsArray[(i * (this._countDotsW + 1)) + j];
                dot.style.top = (i * offsetH) + 'px';
                dot.style.left = (j * offsetW) + 'px';
            }
        }

    }
}