import Helper from '../base/Helper'

export default class Modal {
    constructor(selector, config) {
        this._default = {
            step: 18,
            dotDiameter: 2,
            rate: 25
        };

        this._field = document.querySelector(selector);

        this._initConfig(config);
        this._initField();
    }

    _initConfig(config) {
        if(config) {
            this._config = {};
            for(let prop in this._default) {
                this._config[prop] = config[prop] ? config[prop] : this._default[prop];
            }
        } else {
            this._config = this._default;
        }
    }

    _initField() {
        this._updateField();

        window.addEventListener('resize', (e) => {
            e.isArtificial || this._updateField();
        });
    }

    _updateField() {
        let filedGeometry = this._field.getBoundingClientRect();
        let fieldW = filedGeometry.width;
        let fieldH = filedGeometry.height;
        let step = this._config.step;

        let offsetX = 0;
        let offsetY = 0;

        let content = '';

        while(offsetY < fieldH) {
            while (offsetX < fieldW) {
                if(Helper.getRandomInt(0, 100) < this._config.rate) {
                    content += this._getFragmentContent(offsetX, offsetY);
                }

                offsetX += step;
            }

            offsetX = 0;
            offsetY += step;
        }

        this._field.innerHTML = content;
    }

    _getFragmentContent(offsetX, offsetY) {
        return `<div class="dots__fragment" style="left: ${offsetX}px; top: ${offsetY}px; width: ${this._config.dotDiameter}px; height: ${this._config.dotDiameter}px;"></div>`;
    }

    getElement() {
        return this._field;
    }
}