import Modal from '../../components/Modal'
import Tabs from '../../components/Tabs'
import Dots from '../../components/Dots'

export default class Events {
    constructor(root, config) {
        this._root = root;
        this._config = config;

        this._events = document.querySelector('.events');

        if(!this._events) return;

        this._modal = new Modal(this._config.modal);
        this._tabs = new Tabs(this._config.tabs);
        this._dots = new Dots(this._config.dotsSelector);

        this._initSphere();
    }

    _initSphere() {
        this._spere = this._events.querySelector('.events__sphere');

        this._spere.addEventListener('click',
            (e) => {
                this._modal.open();
                e.preventDefault();
            }
        );

        setTimeout(
            () => this._spere.classList.add('events__sphere--active')
        , this._config.sphereAnimationDelay);
    }
}