    _initSpaceManAnimation(){
        let spacemans = document.querySelectorAll('.js-spaceman');
        let startTime = 1000;
        let offsetTime = 500;
        if(spacemans){
            let showSpaceMan = (index) => {

                if(index < spacemans.length) {
                    switch (index) {
                        case 0:
                            spacemans[index].classList.add('tobacco__decorations-spaceman--active');
                            setTimeout( showSpaceMan, 800, index+1);
                            break;
                        case 1:
                            spacemans[index-1].classList.remove('tobacco__decorations-spaceman--active');
                            setTimeout( () => {
                                spacemans[index].classList.add('tobacco__decorations-spaceman--active');
                                setTimeout( showSpaceMan, 450, index+1);
                            }, 250);
                            break;
                        case 2:
                            setTimeout(() => {
                                spacemans[index-1].classList.remove('tobacco__decorations-spaceman--active');
                            }, 30);
                            spacemans[index].classList.add('tobacco__decorations-spaceman--active');
                            setTimeout( showSpaceMan, 700, index+1);
                            break;
                        case 3:
                            spacemans[index-1].classList.add('tobacco__decorations-spaceman--hide');
                            spacemans[index].classList.add('tobacco__decorations-spaceman--show');
                            setTimeout(() => {
                                this._leaf.classList.add('tobacco__leaf--active');
                            }, 100 );
                            break;
                    }
                }
            };

            setTimeout( showSpaceMan, startTime, 0);

            // spacemans.forEach( (item, index, list) => {
            //     setTimeout( () => {
            //         switch (index){
            //             case 0: break;
            //             case 1: break;
            //             case 2: break;
            //             case 3: break;
            //         }
            //         /*console.log(index);
            //
            //         if (index < spacemans.length - 1){
            //             item.classList.add('tobacco__decorations-spaceman--active');
            //         } else {
            //             list[index-1].classList.add('tobacco__decorations-spaceman--hide')
            //             item.classList.add('tobacco__decorations-spaceman--show');
            //         }
            //
            //         setTimeout( () => {
            //             if(index < spacemans.length-2) {
            //                 item.classList.remove('tobacco__decorations-spaceman--active');
            //             }
            //         }, 100 );*/
            //     }, startTime );
            //     startTime += (offsetTime * 2);
            // } );
            // setTimeout(() => {
            //     this._leaf.classList.add('tobacco__leaf--active');
            // }, startTime - 100 );
        }
    }

    _initBgAnimation(){
        let bgScenes = document.querySelectorAll('.slider__bg-scene');
        console.log(bgScenes);
        let currScene = -1;
        let startTime = 100;
        let offsetTime = 2000;
        //console.log(bgScenes);
        if(bgScenes){
            let showNextScene =  () => {
                if(currScene > -1)
                    bgScenes[currScene].classList.remove("slider__bg-scene--active");
                currScene++;
                let currBgScene = bgScenes[currScene];
                currBgScene.classList.add('slider__bg-scene--active');
                let bgImg = currBgScene.style.backgroundImage;
                bgImg = bgImg.replace(/.*\s?url\([\'\"]?/, '').replace(/[\'\"]?\).*/, '');
                if(bgImg.length > 0) {
                    currBgScene.style.backgroundImage = `url('${bgImg}?${Date.now()}')`;
                }

                if(currScene < (bgScenes.length - 1)) {
                    setTimeout(showNextScene, offsetTime);
                }
            };
            setTimeout( showNextScene, startTime );
        }
    }

    _initLeaf() {
        this._leaf = document.querySelector('.tobacco__leaf');
        if(this._leaf) {
            this._leaf.addEventListener('click',
                (e) => {
                    this._modal.open();

                    e.preventDefault();
                }
            );
            this._startLeafAnimation();
        }
    }

