import Modal from '../../components/Modal'
import OwlSlider from '../../components/OwlSlider'
import Dots from '../../components/Dots'

export default class UniverseGrid {
    constructor(root, config, configGlobal) {
        this._root = root;
        this._config = config;
        this._configGlobal = configGlobal;

        this._owlSlider = new OwlSlider(this._config.owlSliderSelector, this._configGlobal.owlSliderConfig);
        this._dots = new Dots(this._config.dotsSelector);

        this._initModal();
    }

    _initModal() {
        let tastes = this._root.querySelectorAll('.universe-grid__taste');

        this._modal = new Modal(this._config.modal);

        if(tastes) {
            for(let taste of tastes) {
                taste.addEventListener('click',
                    (e) => {
                        this._owlSlider.moveTo(taste.getAttribute('data-position'));
                        this._modal.open();
                        e.preventDefault();
                    }
                );
            }
        }
    }
}