import Dots from '../components/Dots'

export default class Layout {
    constructor(config) {
        this._config = config;

        this._header = document.querySelector('.header');
        this._navToggle = this._header.querySelector('.header__navigation-toggle');

        this._nav = document.querySelector('.navigation');

        if(this._header && this._nav) {
            this._initSmallScreen();
        }
    }

    _initSmallScreen() {
        this._isFirstOpen = true;

        this._navToggle.addEventListener('click', () => {
            this._toggleNavigation();
        });
    }

    _toggleNavigation() {
        this._navToggle.classList.toggle('header__navigation-toggle--open');
        this._nav.classList.toggle('navigation--open');

        document.body.classList.toggle('root-cropped');
        document.documentElement.classList.toggle('root-cropped');

        if(this._isFirstOpen) {
            this._isFirstOpen = false;
            this._dots = new Dots(this._config.dotsSelector);
        }
    }
}