import Helper from '../base/Helper'

export default class Tabs {
    constructor(config) {
        this._default = {
            closeAnimationDuration: 200,
            openAnimationDelay: 30
        };

        this._config = config;
        this._tabs = document.querySelector(config.selector);

        this._initNavigation();
        this._initTabsContent();
    }

    _initNavigation() {
        let tabsNav = this._tabs.querySelector('.tabs__navigation');
        let navLinks = tabsNav.querySelectorAll('.tabs__navigation-button');
        let eventSwitch = Helper.createNewEvent('tabSwitch');

        for(let link of navLinks) {
            link.addEventListener('click',
                (e) => {
                    eventSwitch.tabSelector = link.getAttribute('data-reference');
                    this._tabs.dispatchEvent(eventSwitch);

                    tabsNav.querySelector('.tabs__navigation-button--active')
                        .classList.remove('tabs__navigation-button--active');
                    link.classList.add('tabs__navigation-button--active');

                    e.preventDefault();
                }
            );
        }
    }

    _initTabsContent() {
        let tabsContent = this._tabs.querySelector('.tabs__content');

        this._tabs.addEventListener('tabSwitch',
            (e) => {
                let oldActiveTab = tabsContent.querySelector('.tabs__item--active');
                let newActiveTab = tabsContent.querySelector(e.tabSelector);

                oldActiveTab.classList.remove('tabs__item--active');
                oldActiveTab.classList.add('tabs__item--animate');

                let promise = new Promise(
                    (resolve) => {
                        setTimeout(
                            () => {
                                oldActiveTab.classList.remove('tabs__item--animate');
                                newActiveTab.classList.add('tabs__item--animate');
                                resolve();
                            }
                        , this._default.closeAnimationDuration);
                    }
                );

                promise.then(
                    () => {
                        setTimeout(
                            () => {
                                newActiveTab.classList.add('tabs__item--active');
                                newActiveTab.classList.remove('tabs__item--animate');
                            }
                        , this._default.openAnimationDelay);
                    }
                );
            }
        );
    }
}