import Config from './base/Config'
import Layout from './layout/Layout'

import './pages/desctop/Accessories'
import './pages/desctop/Chart'
import './pages/desctop/ContactUs'
import './pages/desctop/Enter'
import './pages/desctop/Events'
import './pages/desctop/Home'
import './pages/desctop/Mixes'
import './pages/desctop/Tobacco'
import './pages/desctop/UniverseGrid'
import './pages/desctop/UniversePlanet'

import './pages/mobile/AccessoriesMobile'
import './pages/mobile/ContactUsMobile'
import './pages/mobile/EventsMobile'
import './pages/mobile/MainMobile'
import './pages/mobile/MapMixesMobile'
import './pages/mobile/TobaccoMobile'
import './pages/mobile/UniverseGridMobile'
import './pages/mobile/WhereBuyMobile'

export default class Application{
    constructor(){
        let page = document.querySelector('[data-page]');

        this._layout = new Layout(Config.getLocal('Layout'));

        if(page){
            let pageName = page.getAttribute('data-page');
            let pageFolder = /Mobile/.test(pageName) ? 'mobile' : 'desctop';

            new (require(`./pages/${pageFolder}/${pageName}`).default)(page, Config.getLocal(pageName), Config.getGlobal());
        }
    }
}

new Application();
