import Helper from '../../base/Helper'
import Modal from '../../components/Modal'
import OwlSlider from '../../components/OwlSlider'
import Dots from '../../components/Dots'

export default class Chart {
    constructor(root, config, configGlobal) {
        this._root = root;
        this._config = config;
        this._configGlobal = configGlobal;
        // $('.chart__calendar').children().css("opacity",0);

        //this._dots = new Dots(this._config.dotsSelector, {rate: 35});


        var cl = $('.chart__dots').css("clip")
        console.log(cl);
        let height =  $('.chart__dots').outerHeight();
        cl =  "rect(0px, 0px, "+height+"px"+", 0px)";
        $('.chart__dots').css("transition", "0.2s");
        $('.chart__dots').css("clip", cl);



        this._title = this._root.querySelector('.chart__title');

        this._initCalendar();

        window.addEventListener("load", ()=>{

             this._initAnimation();

        })

    }

    _initCalendar() {

        this._calendar = this._root.querySelector('.chart__calendar');
        this._monthes = this._calendar.querySelectorAll('.chart__calendar-month');

        this._owlSlider = new OwlSlider(this._config.owlSliderSelector, this._configGlobal.owlSliderConfig);
        this._modal = new Modal(this._config.modal);

        for(let month of this._monthes) {
            month.addEventListener('click',
                (e) => {
                    this._modal.open();
                    e.preventDefault();
                }
            );
        }
    }

    _initAnimation() {



         this._hand = this._root.querySelector('.chart__decoration-hand');
        //
         this._handhidden = this._root.querySelector('.chart__decoration-hand2');
         this._startWindowWidth = 1280;
         this._startCalendarWidth = this._calendar.getBoundingClientRect().width;

        // this._resizeCalendar();
        // this._limitHand();
        //  $.fn.fadeInDelay = function(){
        //         var init = function(){
        //             $(this).hide().delay($(this).data('delay')).fadeIn();
        //         };
        //         return this.each(init);
        //     };
        //
        //
        // // $('.navigation__list').fadeInDelay();

        window.addEventListener('resize',
            () => {
                setTimeout( () => {
                    this._resizeCalendar();
                    this._limitHand();
                }, 200);
            }
        );
         this._limitHand();

        let coefH = window.innerHeight > 800 ? 1 : 0.94
        let scale = window.innerWidth  / this._startWindowWidth * coefH;
        let handGeometry = this._handhidden.getBoundingClientRect();
        let coefficient = window.innerWidth <= 1366 ? 1.44 : 1.75;

        Helper.setTransform(this._calendar, `scale(${scale})`);
        this._calendar.style.right = `${(handGeometry.width * coefficient) + (window.innerWidth - handGeometry.right)
        + ((this._calendar.getBoundingClientRect().width - this._startCalendarWidth * Math.pow(coefH,2)) / 2)}px`;
        console.log(scale+" " + this._calendar.style.right)
            // this._calendar.classList.add('chart__calendar--show');




        $(function() {
            setTimeout( () => {
                $('.chart__calendar').children().each(function (i) {

                    $(this).delay((i++) * 80).fadeTo(1000, 1);
                })
            }, 330)

        })
        let width =  $('.chart__dots').width();
        let height =  $('.chart__dots').outerHeight();
        console.log(width);
        console.log(height);
        setTimeout( () => {
            var cl = $('.chart__dots').css("clip")
            console.log(cl);
            cl =  "rect(0px, 0px, "+height+"px"+", 0px)";
            var cl2 =  "rect(0px, "+width+"px"+", "+height+"px"+", 0px)";
            console.log(cl);
            $('.chart__dots').css("transition", "3s");
             $('.chart__dots').css("clip", cl2);


        }, 400);
            // clip":""
        // $('.chart__dots').addClass("chart__dots--show");
         // $(".chart__dots").hide();
        // $(".chart__dots").show("slide", { direction: "right" }, 3000);

    }

    _resizeCalendar() {

        let coefH = window.innerHeight > 800 ? 1 : 0.94
        let scale = window.innerWidth  / this._startWindowWidth * coefH;
        let handGeometry = this._hand.getBoundingClientRect();
        let coefficient = window.innerWidth <= 1366 ? 1.44 : 1.75;

        Helper.setTransform(this._calendar, `scale(${scale})`);
        this._calendar.style.right = `${(handGeometry.width * coefficient) + (window.innerWidth - handGeometry.right)
                                + ((this._calendar.getBoundingClientRect().width - this._startCalendarWidth * Math.pow(coefH,2)) / 2)}px`;
        console.log(scale+" " + this._calendar.style.right)

    }

    _limitHand() {
        this._handhidden.style.maxHeight = `${window.innerHeight - this._title.offsetHeight - 20}px`;
          this._hand.style.maxHeight = `${window.innerHeight - this._title.offsetHeight - 20}px`;
    }
}
