import Helper from '../base/Helper'

export default class Slider {
    constructor(config) {
        this._config = config;
        this._step = 0;

        this._slider = document.querySelector(this._config.selector);
        this._sliderContainer = this._slider.querySelector('.slider__container');
    }

    toNext() {
        let slideActive = this._sliderContainer.querySelector('.slider__slide--active');

        if(slideActive) {
            let newSlideActive = slideActive.nextElementSibling;

            slideActive.classList.remove('slider__slide--active');

            if(newSlideActive) {
                newSlideActive.classList.add('slider__slide--active');

                Helper.setTransform(this._sliderContainer, 'translateY(-' + (++this._step) * 100 + '%)');
            }
        }
    }
}