import Helper from '../base/Helper'

export default class Modal {
    constructor(config) {
        this._modal = document.querySelector(config.selector);

        this._resizeEvent = Helper.createNewEvent('resize');
        this._resizeEvent.isArtificial = true;

        if(!this._modal) return;

        this._modal.querySelector('.modal__close').addEventListener('click',
            () => this.close()
        );
    }

    open() {
        this._modal.classList.add('modal--open');

        document.body.classList.add('root-cropped');
        document.documentElement.classList.add('root-cropped');
        window.dispatchEvent(this._resizeEvent);
    }

    close() {
        this._modal.classList.remove('modal--open');

        document.body.classList.remove('root-cropped');
        document.documentElement.classList.remove('root-cropped');
        window.dispatchEvent(this._resizeEvent);
    }
}