import Helper from '../base/Helper'

export default class Switcher {
    constructor(config) {
        this._config = config;

        this._initSwitcher();
    }

    _initSwitcher() {
        this._switcher= document.querySelector(this._config.selector);

        let switcherScale = this._switcher.querySelector('.switcher__scale');
        let switcherItems = this._switcher.querySelectorAll('.switcher__item');

        let eventSwitch = Helper.createNewEvent('switcherSwitch');
        let eventClick = Helper.createNewEvent('click');

        switcherScale.innerHTML = this._getSwitcherScaleContent();
        let switcherScaleValues = switcherScale.querySelectorAll('li');

        for(let item of switcherItems) {
            item.addEventListener('click',
                (e) => {
                    let switcherActiveValues = switcherScale.querySelectorAll(item.getAttribute('data-switcher'));

                    this._diactivateSwithcerScale(switcherScaleValues);
                    this._activateSwitcherScale(switcherActiveValues);

                    eventSwitch.tabSelector = item.getAttribute('data-reference');
                    this._switcher.dispatchEvent(eventSwitch);

                    e.preventDefault();
                }
            );
        }

        this._switcher.addEventListener('switcherSwitch',
            (e) => {
                let oldActiveTab = this._switcher.querySelector('.switcher__tab--active');
                let newActiveTab = this._switcher.querySelector(e.tabSelector);

                if(oldActiveTab) {
                    oldActiveTab.classList.remove('switcher__tab--active');
                    oldActiveTab.classList.add('switcher__tab--animate');
                }

                let promise = new Promise(
                    (resolve) => {
                        setTimeout(
                            () => {
                                oldActiveTab && oldActiveTab.classList.remove('switcher__tab--animate');
                                newActiveTab.classList.add('switcher__tab--animate');
                                resolve();
                            }
                            , this._config.closeAnimationDuration);
                    }
                );

                promise.then(
                    () => {
                        setTimeout(
                            () => {
                                newActiveTab.classList.add('switcher__tab--active');
                                newActiveTab.classList.remove('switcher__tab--animate');
                            }
                            , this._config.openAnimationDelay);
                    }
                );
            }
        );

        this._switcher.querySelector('.switcher__item:first-child')
            .dispatchEvent(eventClick);
    }

    _activateSwitcherScale(switcherActiveValues) {
        for(let item of switcherActiveValues) {
            item.classList.add('active');
        }
    }

    _diactivateSwithcerScale(switcherScaleValues) {
        for(let item of switcherScaleValues) {
            item.classList.remove('active');
        }
    }

    _getSwitcherScaleContent() {
        let content = '';
        let baseHeight = this._config.switcherBaseHeight;
        let stepNumber = this._config.switcherStepNumber;
        let step = this._config.switcherStep;

        for(let counter = 0; counter < stepNumber; counter++) {
            let height = baseHeight + counter * step;
            let extraClasses = 'rear ';

            extraClasses += counter < 6 ? 'soft ' : '';
            extraClasses += counter < 13 ? 'medium' : '';

            content += `<li style="height: ${height}px" class="${extraClasses}"></li>`;
        }

        return content;
    }
}