export default class OwlSlider {

    constructor(selector, option){
        this._option = option || {};

        if(!!selector) {
            this._sliders = $(selector);
            this._sliders.each( (index, _slider) => {
                let slider = $(_slider).owlCarousel(this._option)
            } )
        }
    }

    moveTo(position) {
        this._sliders.trigger('to.owl.carousel', [position, 0]);
    }
}