
export default class ContactUs{
    constructor(root, config, configGlobal){
        this._root = root;
        this._config = config;

        this._initAnimation();
    }

    _initAnimation(){
        let spaceManSeq = this._root.querySelectorAll('.js-contact-astro');

        if(spaceManSeq){
            let startAnimate = 100;
            let offsetAnimate = 1000;

            spaceManSeq.forEach( (item) => {
                setTimeout( () => {
                    spaceManSeq.forEach( item => item.classList.remove('contact__astro-img--active') );
                    item.classList.add('contact__astro-img--active');
                }, startAnimate );
                startAnimate += offsetAnimate;
            } );

            function getRandomInRange(min, max) {
                return Math.floor(Math.random() * (max - min + 1)) + min;
            }


            let src =  $('.js-contact-astro').attr("src");
            let temp = src.split('?')
            src = temp[0]
            $('.js-contact-astro').attr("src", '');
            let rand = getRandomInRange(1, 1000);
            src += '?'+rand
            console.log(src);
            $('.js-contact-astro').attr("src", src);

        }
    }
}
