import EasingFunction from './EasingFunction';

export default class Canvas{

    static SHADERS = {
        'OUTLINE': {
            uniforms: {
                offset: {
                    type: "f",
                    value: 0.05
                }
            },
            vertexShader: [
                "uniform float offset;",
                "void main() {",
                "vec4 pos = modelViewMatrix * vec4( position + normal * offset, 1.0 );",
                "gl_Position = projectionMatrix * pos;",
                "}"
            ].join("\n"),

            fragmentShader: [
                "void main(){",
                "gl_FragColor = vec4( 1.0, 1.0, 1.0, 1.0 );",
                "}"
            ].join("\n")
        },
    };

    constructor(root, config){
        this._root = root;
        this._config = {
            camera: {
                position:   ((config.camera && config.camera.position)? config.camera.position : new THREE.Vector3(0,0,0,)),
                lookAt:     ((config.camera && config.camera.lookAt)? config.camera.lookAt : new THREE.Vector3(0,0,0,)),
                size:       ((config.camera && config.camera.size)? config.camera.size : 2)
            },
            light: {
                position:   ((config.light && config.light.position)? config.light.position : new THREE.Vector3(0,0,0,)),
            }
        };
        this._listAnimation = [];

        if(this._root){
            this._initCanvas()
        }
    }

    /**
     * Заргрущка 3д модели в формате jd
     *
     * @param {String} pathToModel
     * @param {function} loaded
     */
    loadModel( pathToModel, loaded ){
        let loader = new THREE.JDLoader();
        this._objects = [];
        this._mixers = [];
        this._clips = [];
        loader.load( pathToModel,
            data => {
                // console.log(data);
                let multiMaterial = new THREE.MultiMaterial(data.materials);
                for(let i = 0; i < data.geometries.length; i++){
                    let geometry = data.geometries[i];
                    let mesh = new THREE.SkinnedMesh(geometry, multiMaterial);
                    if(geometry.animations){
                        let mixer = new THREE.AnimationMixer(mesh);
                        this._mixers.push(mixer);
                        let clip = mixer.clipAction(geometry.animations[0]);
                        clip.play();
                        clip.paused = true;
                        this._clips.push(clip);
                    }
                    this._objects.push(mesh);
                    this._scene.add(mesh);
                }
                this._animateToDelta(0);
                if(typeof loaded === 'function'){
                    loaded();
                }
            }
        )
    }

    getMeshes(){
        return this._objects;
    }

    removeMesh(ind){
        if(ind > -1 && ind < this._objects.length) {
            this._scene.remove(this._objects[ind]);
        }
    }

    addShaderToMesh(indexMesh, shader){
        if(indexMesh > -1 && indexMesh < this._objects.length) {
            //console.log(this._objects[indexMesh]);
            let geometry = this._objects[indexMesh].geometry;
            let matShader = new THREE.ShaderMaterial(shader);

            let mesh = new THREE.SkinnedMesh(geometry,matShader);
            mesh.material.depthWrite = false;
            this._outScene = new THREE.Scene();
            this._outScene.add(mesh);
            this._objects.push(mesh);
        }
    }

    /**
     * Rotate scene
     *
     * @param {THREE.Vector3} vector
     */
    rotateScene(vector){
        for(let i = 0; i < this._objects.length; i++) {
            this._objects[i].rotation.x = vector.x;
            this._objects[i].rotation.y = vector.y;
            this._objects[i].rotation.z = vector.z;
        }
    }

    startAnimationLoop(){
        let clock = new THREE.Clock();
        let animation = () => {
            let delta = clock.getDelta();
            this._animateToDelta(delta);
            requestAnimationFrame(animation)
        };
        requestAnimationFrame(animation);
    }

    _animateToDelta(delta){
        for(let i = 0; i < this._mixers.length; i++){
            this._mixers[i].update(delta);
        }
    }

    animateToTime(time){
        //if(this._idAnimate) clearTimeout(this._idAnimate);
        for(let i = 0; i < this._mixers.length; i++){
            this._animateTo(i,time);
        }
    }

    _animateTo(i, time){

        if(this._listAnimation[i]){
            this._listAnimation[i].curr = 0;
            this._listAnimation[i].end = 1;
            this._listAnimation[i].startClipTime = this._clips[i].time;
            this._listAnimation[i].timeBetween = time - this._clips[i].time;
            return;
        }

        this._listAnimation[i] = {
            curr: 0,
            end: 1,
            clock: new THREE.Clock(),
            startClipTime: this._clips[i].time,
            timeBetween: time - this._clips[i].time
        };

        let animation = () => {
            let animParam = this._listAnimation[i];
            if(animParam.curr < animParam.end){
                animParam.curr += animParam.clock.getDelta();
                this._mixers[i].update(0);
                let ease = EasingFunction.linear(animParam.curr / animParam.end);
                let newTime = animParam.startClipTime + (animParam.timeBetween * ease);
                if(newTime < 0) newTime = 0;
                this._clips[i].time = newTime;
                requestAnimationFrame(animation);
            } else {
                delete this._listAnimation[i];
            }
        };

        requestAnimationFrame(animation);
    }

    _initCanvas(){
        this._createRenderer();
        this._startRender();
    }

    _startRender(){
        // MAIN LOOP FOR RENDER
        let render = () => {
            if(this._outScene)
                this._renderer.render( this._outScene, this._camera );
            this._renderer.render( this._scene, this._camera );
            requestAnimationFrame(render);
        };
        render();
    }

    // создание камеры
    _createCamera(){
        let size = this._config.camera.size;
        let ratio = window.innerWidth / window.innerHeight;
        this._camera = new THREE.OrthographicCamera(
            (size * ratio) / -2,
            (size * ratio) / 2,
            size / 2,
            size / -2,
            0.00000000000001,
            1000
        );
        this._camera.position.copy(this._config.camera.position);
        this._camera.lookAt(this._config.camera.lookAt);

        window.addEventListener('resize', () => {
            this._camera.aspect = window.innerWidth / window.innerHeight;
            this._camera.updateProjectionMatrix();
            this._renderer.setSize( window.innerWidth, window.innerHeight );
        })
    }

    // создание канвы для рендера
    _createRenderer(){
        // create scene
        this._scene = new THREE.Scene();

        // create lights
        let pointLight = new THREE.PointLight(0xffffff, 1);                         //(color, intensity, distance, decay )
        let hemisphereLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 1.0);   //( skyColor, groundColor, intensity )
        pointLight.position.copy(this._config.light.position);
        hemisphereLight.position.set(0, 10000, 0);

        // add lights to scene
        // this._scene.add(pointLight);
        this._scene.add(hemisphereLight);
        // this._scene.background = new THREE.Color( 0xff0000 );

        // create renderer
        this._renderer = new THREE.WebGLRenderer({ format: THREE.RGBAFormat, alpha: true, antialiasing: true, antialias: true });
        this._renderer.setSize(window.innerWidth, window.innerHeight);
        this._renderer.setPixelRatio(window.devicePixelRatio);
        this._renderer.setClearColor(0x000000, 0.0);
        this._renderer.autoClear = false;
        this._renderer.gammaInput = true;
        this._renderer.gammaOutput = true;

        this._root.appendChild(this._renderer.domElement);

        this._createCamera();

        // Controls
        // let controls = new THREE.OrbitControls( this._camera, this._renderer.domElement );
        // controls.target.set( 0, 1, 0 );
        // controls.update();
    }
}